<template>
    <div class="top-wrap" v-show="show" @click="goTop()">
        <div class="up iconfont icon-up"></div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      show: false
    };
  },
  methods: {
    goTop() {
      document.documentElement.scrollTop = document.body.scrollTop = 0;
    },
    handleScroll() {
      let top = document.documentElement.scrollTop || document.body.scrollTop;
      if (top > 200) {
        this.show = true;
      } else {
        this.show = false;
      }
    }
  },
  mounted() {
    setInterval(() => {
      this.handleScroll();
    });
  }
};
</script>

<style lang="scss" scoped>
	.top-wrap {
        width: 50px;
        height: 50px;
		position: fixed;
		top: 45%;
		right: 3%;
		border-radius: 50%;

		background-color: rgba(0, 0, 0,0.2);
    z-index: 99;

	}
	.up {
		font-size: 30px;
    position: absolute;
    right: 20%;
    top: 35%;
	}
</style>